import React from 'react'

export default function Skill (props){
    return (
        <li>
<h1>{props.nome}</h1>
            <div className="pr">
                <div className="bar" style={{width:props.porcentagem+'%'}}>
                    <h2>{props.porcentagem}%</h2>
                </div>
            </div>
        </li>
    )
}